*{
--section_height: fit-content;
font-size:16px;
--fontsize_h1: clamp(4vmin, 3.052rem,7vmin);
--fontsize_h2: clamp(5vmin, 2.441rem, 6vmin);
--fontsize_h3: 1.953rem;
--fontsize_h4: 1.563rem;
--fontsize_h5: 1.25rem;
--fontsize_h6: 0.875rem;
--fontsize_body: 0.875rem;
--tontsize_small: 0.75rem;
--typeface_heading: 'Inter', sans-serif;
--typeface_body: 'IBM Plex Sans', sans-serif;

--textColor: var(--dark01);
--bgColor: var(--bright01);
--blue01: #14A1DE;
--blue02: #0066b0;
--link_idle: var(--textColor);
}

body{
  margin:0;
  padding:0;
  color: var(--textColor);
  background:var(--bgColor);
 
}
figure{
  margin:0;
  padding:0;
}
.heading_01{
  font-size: var(--fontsize_h1);
  font-family: var(--typeface_heading);
  font-weight:500;
  line-height:1.5;
  margin:0;
  min-height:1rem;
}
.paragraph__ul {
  
  margin:auto;
}
.paragraph__ul *{
  list-style-type:circle;
  text-align:left;
  
}
.left_align{
  text-align:left;
}
.section--left_align *{
  text-align:left;
}
