.text_body{

    max-width:60rem;
    width:75%;
    /*margin:auto;*/
    margin-bottom:2rem;
}
.text_small{
  font-size: var(--fontsize_small);
  font-family: var(--typeface_body);
  font-weight:300;
}
.figcaption{
  margin-top:0.5rem;
}
#main_container{
  position:relative;
  width:100%;
  height:auto;
  margin:0;
  padding:0;

}
.paragraph{
  line-height:1.8;
}
.paragraph a{
  color:inherit;
}
.slide_hero{
    
    padding:10%;
    padding-top:5%;
    margin:0;
    background: var(--bright01);
    background-repeat:repeat, no-repeat;
    background-size: 1.5rem 1.5rem, 100% 100%;
    background-image: radial-gradient(circle,rgba(0,0,0,.15) 0,rgba(0,0,0,.15) 0.07rem,transparent 0),radial-gradient(circle,transparent 50%,rgba(107,225,241,.15) 75%,rgba(255,43,109,.15) 100%);
    
 
}
.hero_img{
    
    
    width:100%;
    height:auto;
    max-height:40rem;
    object-fit: cover;
    margin:auto;
    padding:0;
    grid-row-start:2;
    grid-row-end:3;
    overflow:visible;
}
#avinvadas_logo{
  width:3rem;
  height:3rem;
  position:fixed;
  z-index:2;
  display:flex;
  justify-content:center;
  align-content:center;
}
.logo_link{
  position:relative;
  width:2rem;
  height:2rem;
  margin:auto;
  align-self:center;
  color:inherit;
}
.product_slide{
  padding:0;
  margin:auto;
  width:100%;
  height: fit-content;

  display:grid;
  grid-template-columns: auto 50% auto;
  grid-template-rows: 25% 50% 25%;
  padding-bottom: 3rem;
  
}
.main_header{
  grid-column-start:2 ;
  grid-column-end:3 ;
  grid-row-start:1 ;
  grid-row-end: 2 ;
  text-align:left;

}
.slide__heading{
  margin:0;
  margin-top:1.33rem;
  margin-bottom:0.66rem;
  padding:0;
  color:var(--textColor);
}
.slide__content{
  color:var(--textColor);
  width:100%;
  max-width:80vmin;
  height:fit-content;
  grid-column-start:2 ;
  grid-column-end:3 ;
  grid-row-start:2 ;
  grid-row-end: 3 ;
  margin:auto;
  text-align:left;
  margin-top:4rem;
}

.slide__content--fullRange{
  color:var(--textColor);
  width:100%;
  height:fit-content;
  grid-column-start:1 ;
  grid-column-end:5 ;
  grid-row-start:1 ;
  grid-row-end: 3 ;
  margin:2rem  0;
  
  text-align:center;

}

.slide__content--projects{
  grid-column-start:2 ;
  grid-column-end:3 ;
  grid-row-start:1 ;
  grid-row-end: 3 ;
}

.fullRange__image{
  width:100%;
  height:auto;
  min-height:50vmax;
  overflow:hidden;
background:inherit;
margin-top:7rem;
}
.fullRange__image img{
  width:100%;
  height:auto;
  min-height:80vmin;
  object-fit: cover;
  margin:0;
  padding:0;

}
.images_x2{
  width:100%;
  display:grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;

  row-gap: 0.2rem;
  column-gap: 0.2rem;
  align-self:center;
  margin:auto;
  margin-top:4rem;
}
.image_single{
  margin:auto;
  margin-top:2rem;
  max-width:60vw;
}
.gallery_image{
  position:relative;
  padding:0;
  margin:0;
  width:100%;
  height:auto;
  border: solid 1px var(--bright03);
}
.project_li__data{
  display:flex;
  text-align:left;
  align-items:flex-start;

  margin-top:1rem;
  display:flex;
  flex-direction:column;
  justify-content:flex-end;
}
.project_li__data .heading_06{

margin-bottom:0.5rem;
}
.pretext{
    grid-row-start:1;
    grid-row-end:2;
    grid-column-start:1;
    grid-column-end:2;
    margin:auto;
    place-self:center;
    text-align:center;
    
    margin-top:3rem;
}
.copyright{
  margin-top:10rem;
  opacity:0.66;
}
.text_and_image{
  grid-row-start:2;
  grid-row-end:3;
  grid-column-start:1;
  grid-column-end:3;
  margin:auto;
  padding:0;
  display:grid;
  grid-template-columns:1fr 3fr;
  grid-template-rows:1fr;
  place-items:center;
  text-align:left;

  width:100%;
  margin:auto;

  /*display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  justify-content: space-around;*/

}
.content_2segs{
  display:grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  place-items:center;

}
.content_2segs *{
  margin:auto;

}
.content_2segs :last-child{
  padding-left:1rem;
  /*border-left: solid 1px var(--textColor);*/
}

.text_and_image *{
margin:auto;
padding:0;
}
.li_contact{
  font-size: var(--fontsize_body);
  font-family: var(--typeface_body);
  color: var(--textColor);
}
.li_contact a, .internal_heading{
  font-size: var(--fontsize_body);
  font-family: var(--typeface_body);
  color: var(--textColor);
}
.icons_container{
  grid-column-start:1 ;
  grid-column-end:4 ;
  grid-row-start:2 ;
  grid-row-end: 3 ;
  display:grid;
  grid-template-columns: auto 40vmin auto;
  grid-template-rows:  15vmin 40vmin 50vmin;
  width: 80vmin;
  height: 80vmin;
  place-self:center;
}
.pretext{

}
.pretext *{
margin:0.0rem;

}
.titles_binder{
  width:100%;
  height:100%;
  grid-column-start:1 ;
  grid-column-end:4 ;
  grid-row-start:3 ;
  grid-row-end: 4 ;
  display:flex;
  flex-direction:column;
  height:fit-content;
text-align:center;

}
.product_title{
  text-align:center;
  vertical-align: text-top;
  color:var(--textColor);
  height:fit-content;
  margin:auto;
  margin-top:1rem;
}
#product_subtitle{
  text-align:center;
  color:var(--textColor);
  height:fit-content;
  margin:Auto;
  margin-top:1rem;
width:100%;
}
#icons_slide{
  height: 100vh;
}
.logos_column{
  margin-top:5rem;
  margin-bottom:5rem;
}
.gallery_image{
  width:100%;
  height:auto;
}

.product_icon{
grid-column-start:2 ;
grid-column-end:3 ;
grid-row-start:2 ;
grid-row-end: 3 ;
background:transparent;
color:var(--blue01);
margin:auto;
padding:0;
width:100%;
height:100%;
opacity:1;

}

.borderless{
    border: none;
}
#AppScan{
  animation: fadein 1s forwards;
}
@keyframes fadein{
  from{
    opacity:0;
  }
  to{
    opacity:1;
  }
}
.product_visible{
  opacity:1;
}
.product_hidden{
  opacity:0;
}
.GO_shapeFill{
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  -webkit-animation: dash 1s linear forwards;
          animation: dash 1s linear forwards;
}
@keyframes dash{
  from{
    stroke-dashoffset:1600;
  }
  to{
    stroke-dashoffset:0;
  }
}
@-webkit-keyframes dash{
  from{
    stroke-dashoffset:1600;
  }
  to{
    stroke-dashoffset:0;
  }
}
.product_icon{
  opacity:0;
}
#AppScan{
  opacity:1;
  transform:Scale(0.8);
}
#img_logos_base{
  opacity:1;
}
#img_logos_column{
  opacity:1;
  height:fit-content;
  width:60vmin;
  max-width:30rem;
  margin:auto;
}
#slide_system__image01{
  background:var(--blue02);
  opacity:0;
}
.testimonial{
  margin-top:2rem;
  border-top: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  padding:1rem;
}
.testimonial *{
    font-family:var(--tf-body);
  }
.contacts{
  margin:auto;
  height:fit-content;
}
@media only screen and (max-width: 1100px) {


  .text_and_image{
    grid-row-start:2;
    grid-row-end:3;
    grid-column-start:1;
    grid-column-end:3;
    margin:auto;
    padding:0;
    display:grid;
    grid-template-columns:1fr;
    grid-template-rows:2fr 1fr;
    place-items:center;
    text-align:center;
  }
  /*.images_x2{
    width:100%;
    display:flex;
    flex-direction:column;
    flex-flow:wrap;
    justify-content: center;
  }*/
  .content_2segs{
    display:grid;
    width:80vmin;
    grid-template-rows: 50% 50%;
    grid-template-columns: auto;
    place-items:center;
  }
  .content_2segs :last-child{
    padding:none;
    border:none;
    padding-top:1rem;
    /*border-top: solid 1px var(--textColor);*/
    width:60vmin;
  }
  .product_slide{
    padding:0;
    margin:auto;
    width:100%;
    height: var(--section_height);

    display:grid;
    grid-template-columns: auto 80% auto;
    grid-template-rows: 25% 45% 30%;
    padding-bottom: 3rem;
    
  }
  .image_single{
    margin:auto;
    margin-top:2rem;
    max-width:70vw;
  }

}
